<script>
export default {
	data () {
		return {
			caseObj1: {
				showCoreFlag: false,
				coreUrl: 'javascript:void(0)',
			},
			caseObj2: {
				showCoreFlag: false,
				coreUrl: 'javascript:void(0)',
			},
		};
	},
	methods: {
		// 切换代码显隐
		toggleDemoCore (item) {
			item.showCoreFlag = !item.showCoreFlag;
		},
		open1() {
			this.$notify({
				title: '提示',
				message: '这是一条会自动关闭的消息',
				duration: 2000
			});
		},
		open2() {
			this.$notify({
				title: '提示',
				message: '这是一条不会自动关闭的消息',
				duration: 0
			});
		},
	},
}
</script>
